<template>

  <!--{{ tagSpecs }}-->
  <component :is="tagName(tagSpec(tagSpecs, 0))"
             :class="tagClasses(tagSpec(tagSpecs, 0))"
             v-if="isHtml"
             v-show="!_.isEmpty(obfuscatedVal)"
             v-html="obfuscatedVal">
  </component>

  <component :is="tagName(tagSpec(tagSpecs, 1))"
             :class="tagClasses(tagSpec(tagSpecs, 1))"
             v-else
             v-show="!_.isEmpty(content) || _.isNumber(content)"
  >{{ content | obfuscate | bullet }}
  </component>

</template>

<script>
import TextHtml from './TextHtml'

export default {
  extends: TextHtml,
  watch: {
    events: function (fresh, stale) {
      switch (fresh) {
        case 'recorded.miss':
        case 'recorded.hit':
        case 'game.lost':
        case 'game.new':
          this.$write('numberPad', '');
          break;
      }
    }
  },
  computed: {
    events: function () {
      return this.$read('events');
    },
    obfuscatedVal: function () {
      if (this.content == '') {
        return this.$options.filters
            .obfuscate(this.$options.filters
                .bullet('0'));
      }

      return this.$options.filters
          .obfuscate(this.$options.filters
              .bullet(this.content));
    }
  }
}
</script>
