<template>
  <component is="div" v-html="obfuscatedVal"></component>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'tally',
    ]),

    obfuscatedVal: function (){

      return this.$options.filters
          .obfuscate(this.$options.filters
              .bullet(this.tally()));
    }
  }
}
</script>
