<template>
  <div>
    <button @click="input('1')">1</button>
    <button @click="input('2')">2</button>
    <button @click="input('3')">3</button>
    <button @click="input('4')">4</button>
    <button @click="input('5')">5</button>
    <button @click="input('6')">6</button>
    <button @click="input('7')">7</button>
    <button @click="input('8')">8</button>
    <button @click="input('9')">9</button>

    <button @click="input('0')">0</button>
    <button @click="input('.')">.<sub>decimal</sub></button>
  </div>
</template>

<script>
export default {

  props: {
    locus: {
      type: String,
      required: true,
    },

    service: {
      type: String,
      default: ['state', 'nebula'][0]
    }
  },

  methods: {
    input: function (val) {
      switch (this.service) {
        case 'state':
          this.privateStateInput(val);
          break;
        case 'nebula':
          var key = stateTagApp.$read('sta.appId')
              .concat(this.locus);
          this.$nebula(key, val);
          break;
      }
    },
    privateStateInput: function (val) {
      let str = this.$read(this.locus);

      //not leading 0
      if (val == '0' && (str == '')) {
        this.log('leading zero detected');
        return;
      }

      //not extra decimal point
      if (val == '.') {
        if (str == '') {
          str = '0';
        }
        if (str.includes('.')) {
          return;
        }
      }

      str = str.concat(val);
      this.$write(this.locus, str);
    }
  },

  mounted: function () {
    if (this.service == 'state') {
      var key = stateTagApp.$read('sta.appId')
          .concat(this.locus);
      this.$onNebula(key, this.privateStateInput);
    }
  }

}
</script>

