  height: inherit;

  display: grid;
  grid-auto-flow: row;
  grid-template-rows:
  /*min-content*/
  30px
  75px
  235px;

  grid-gap: 30px;
}

.upper,
.lower {
  display: grid;
  grid-auto-flow: column;
  direction: rtl;
}

.bar {
  margin: 0;
  padding: 20px 0 0 0;
  align-self: center;
}

.bar > * {
  /*align-self: center;*/
  /*justify-self: center;*/
}

.upper > *,
.lower > * {
  justify-self: center;
}

.upper > * {
  align-self: start;
}

.lower > * {
  align-self: end;
}
</style>